import { 
  useState,
  useReducer,
  useEffect,
} from 'react'

import axios from 'axios'

export const endpoint = 'https://www.scorebat.com/video-api/v1/'

/**
 * * convert date with time to only date 
 */
export const convertDate = (dateTime) => {
  const date = new Date(dateTime)
  return date.toLocaleDateString() 
}

/**
 * 
 * @param video: video from endpoint API 
 * @param league: selected league
 * * Filter video based on league from parameter
 */
export const filterLeague = (video, league) => video.competition.name === league


/** USE CUSTOM FETCH DATA FROM API */
// Reducer
const dataFetchReducer = (state, action) => {
  switch (action.type) {
    case 'FETCH_SUCCESS':
      return {
        ...state,
        isLoading: false,
        isError: false,
        data: action.payload,
      };
    case 'FETCH_FAILURE':
      return {
        ...state,
        isLoading: false,
        isError: true,
      };
    default:
      throw new Error();
  }
}

// Use Custom
export const useDataApi = (initialUrl, initialData) => {
  const [url, setUrl] = useState(initialUrl);
 
  const [state, dispatch] = useReducer(dataFetchReducer, {
    isLoading: false,
    isError: false,
    data: initialData,
  });
 
  useEffect(() => {
    const fetchData = async () => {
      try {
        const result = await axios(url);
        dispatch({ type: 'FETCH_SUCCESS', payload: result.data });
      } catch (error) {
        dispatch({ type: 'FETCH_FAILURE' });
      }

    };
 
    fetchData();
  }, [url]);
 
  return [state, setUrl]
};