import React from 'react'

import {
  Switch,
  Route,
  Link
} from 'react-router-dom'

import {
  RoutesNavigation,
  NavLeague,
  Video,
  Loading,
  EndPage
} from './App/components/all'

import {
  endpoint,
  useDataApi
} from './App/logic/constant'

import { RiHome2Line } from 'react-icons/ri'

import './App.css'

export const VideosContext = React.createContext() 

export default function FootballVideo() {
  const [videos] = useDataApi(endpoint, [])

  return (
    <VideosContext.Provider value={videos.data}>
    <Switch>
      <Route exact path='/'>
      
        {/** NAVIGASI HOME FIX */}
        <Link 
          to='/' 
          className='fixed-home'>
            <RiHome2Line 
              size='2em' 
              color='black' 
              className='home-fix' 
            />
          </Link>

        {/** NAVBAR LEAGUE */}
        <NavLeague />

        {/* CONTENTS */}
        <h1>Highlights videos of matches from Europe's top leagues</h1>
        <hr/>
        {videos.data.length
          ? <Video result={videos.data} />
          : <Loading custom={{ padding: '35vh 0' }}/>
        }
      
        {/* END PAGE */}
        <EndPage/>

      </Route>
      <RoutesNavigation />
    </Switch>
    </VideosContext.Provider>
  )
}