import React, { useContext } from 'react'

import {
  listLeague,
  navListLeague,
  routeListNavigation
} from './../required-list/list'

import {
  Route,
  Link,
  NavLink,
  Switch
} from 'react-router-dom'

import {
  convertDate,
  filterLeague
} from './../logic/constant'

import { RiHome2Line } from 'react-icons/ri'

import { VideosContext } from './../../App'

import LazyLoad from 'react-lazyload'

import './components.css'

/** SELECTED LEAGUE PAGE */
export function SelectedLeague({ title, league }) {
  const results = useContext(VideosContext)

  return (
    <Switch>
      <Route exact path='/:slug'>

      {/** NAVIGASI HOME FIX */}
      <Link 
        to='/' 
        className='fixed-home'>
          <RiHome2Line 
            size='2em' 
            color='black' 
            className='home-fix' 
          />
      </Link>

      {/** NAVBAR LEAGUE */}
      <NavLeague />

      {/* CONTENTS */}
      <h1>{title}</h1>
      {results.length
        ? <VideoFilter league={league} />
        : <Loading custom={{ padding: '35vh 0' }}/>
      }

      {/* END PAGE */}
      <EndPage/>

      </Route>
    </Switch>
  )
}

/** ROUTE NAVIGATION LEAGUE */ 
export const RoutesNavigation = () => routeListNavigation.map(
  route => (
    <Route
      exact
      key={route.path}
      path={route.path}>
      <SelectedLeague 
        title={route.title} 
        league={route.league}
      />
    </Route>
  )
)

/** NAVBAR LEAGUE */
export const NavLeague = () => {
  return (
    <nav>
    {navListLeague.map(
      item => (
        <NavLink 
          key={item.text} 
          to={item.path}
          className='content-nav-league'
          activeClassName='active-nav'>
          <img 
            src={item.src} 
            alt={item.text} 
            width='30px'
            height='30px'
            className='img-nav-league'
          />
          <p className='text-nav-league'>
            {item.text}
          </p>
        </NavLink>
      )
    )}
    </nav>
  )
}

/** VIDEO */
export const Video = ({ result }) => {
  /**
   * * If video content is empty, render Empty
   */
  if (!result.length) {
    return <Empty/>
  }
  
  return (
    <div className='desktop-content-video'>
    {
      result.map(
        video => <VideoContent key={Math.random()} content={video} />
      )
    }
    </div>
  )
}

/** VIDEO LEAGUE FILTER */
export const VideoFilter = ({ league }) => {
  // Use Context
  const results = useContext(VideosContext)

  /**
   * * Filter video content before render
   */ 
  const alreadyFilter = results.filter(
    video => filterLeague(video, league)
  )

  /**
   * * If video already filter is empty, render Empty
   */
  if (!alreadyFilter.length) {
    return <Empty/>
  }
  
  return (
    <div className='desktop-content-video'>
    {
      alreadyFilter.map(
        video => <VideoContent key={video.title} content={video} />
      )
    }
    </div>
  )
}

/** CONTENT OF VIDEO */
export const VideoContent = ({ content }) => {
  // Destructure Objects from content
  const {
    title,
    videos,
    date,
    competition
  } = content

  return (
    <div className='video-content' key={title}>
      <LazyLoad 
        offset={200}
        height={220}
        once>
      <div 
        dangerouslySetInnerHTML={{__html: videos[0].embed}} 
      />
      </LazyLoad>
      <p className='title'>HIGHLIGHTS | {title}</p>
      <div className='description'>
        <p>{listLeague[competition.id]}</p>
        <p>{convertDate(date)}</p>
      </div>
    </div>
  )
}

/** LOADING CONTENTS */
export const Loading = ({ custom }) => (
  <div className='container-loading' style={custom}>
    <div className='loading'></div>
  </div>
) 

/** EMPTY CONTENTS */
export const Empty = ({ custom, text }) => (
  <div className='container-empty' style={custom}>
    <div className='contents-empty'>
      <img 
        src='https://i.ibb.co/dQPZn7d/not-found.png' 
        alt='No results found'
        width='100px'
        height='100px'
      />
      <div className='message-empty' style={text}>
        No results found
      </div>
    </div>
  </div>
)

/** END PAGE */
export const EndPage = () => (
  <p className='end-page'>
    No videos more
  </p>
)